* {
	padding: 0px;
	margin: 0px;
	border: 0px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

:focus,
:active {
	/* outline: none; */
}

a:focus,
a:active {
	/* outline: none; */
}
html,
body,
#root,
#app,
#app > div {
	width: 100%;
	height: 100%;
	min-width: 100vw;
	min-height: 100vh;
	overflow-x: hidden;
}
html,
body {
	font-size: var(--font-size);
	// min-width: var(--min-width);

	display: flex;
	justify-content: center;
	align-items: center;
}

body {
	/* color: $mainColor; */
	background-color: #000;
	line-height: 1;
	font-family: var(--base-font);
	font-size: var(--font-size);
	/* text-rendering: optimizeLegibility; */
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	display: flex;
	justify-content: center;
	align-items: center;

	font-weight: 500;
	height: 100%;
}

input,
button,
textarea {
	font-family: var(--base-font);
	font-size: inherit;
}

button {
	cursor: pointer;
	color: inherit;
	background-color: inherit;
}

a {
	color: inherit;
}

a:link,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}
input {
	padding: 0.1rem 0.3rem;
}

:root {
	box-sizing: border-box;
	//   font-size: calc(1vw + 0.6em);
	/* font-size: clamp(20px, 2vw, 32px);'Material Symbols Outlined', */

	--max-width: 1080px;

	--base-font: 'Ubuntu', 'Helvetica Neue', sans-serif;
	/* --font-size: calc(1vw + 0.6em); */
	--font-size: clamp(14px, 1vw, 32px);
	--min-width: auto;
	--main-color: #9bc53d;
	--background-form-color: #e9ebee;
	--background-root-color: #000;
	--background-container-color: #dddddd;
	--background-header-color: #d1aee3;
	--background-vk-link-blue: #0077ff;
	--background-google-link-grey: #aaaaaa;
	--background-mail-link-wblue: #168de2;
	--secondary-color: #ebfffa;
	--btn-send-com: #e1579c;
	--form-inp-email: #ffffff;
	--form-border-color: #727272;
	--nik-name-red: red;
	--nik-time-opacity: rgba(179, 179, 179, 0.7);
	--border-bottom: rgba(179, 179, 179, 0.5);
	--color-active-link: #ffffff;
	--background-form-white: #ffffff;

	font-size: var(--font-size);
}
#root {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	flex-grow: 1;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 0;

	background-color: var(--background-root-color);
}
.particles-cnt {
	width: 100%;
}
.particles-canv {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	width: auto;
	height: auto;
}
.wrapper {
	// height: 90vh; //!!
	// height: calc(100vh - 186px); //!!
}
.container {
	max-width: var(--max-width);
	max-height: 100%;
	overflow-y: hidden;
	width: 90vw;
	height: auto;
	background-color: var(--background-container-color);
	margin: 0 auto;
	padding: 0 1rem;

	// overflow: hidden;
}
.container > .login-user-cabinet {
	height: 90vh;
}
.main {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	text-align: center;
	margin: 0 auto;

	flex-grow: 1;
	// min-width: var(--max-width);
	width: 100%;
	height: 100%;
}

main.error,
main.login,
main.registaration {
	box-sizing: border-box;
	width: 100%;
	// height: calc(100vh - 186px); //!!
	height: 90vh;
	overflow: hidden;

	// margin-bottom: 186px;
}
main.anime {
	// height: 100vh;
	padding-top: 1.3rem;
	padding-bottom: 1.6rem;
}
main.full-desc-item {
	padding-top: 1.3rem;
}
main.error,
main.login,
main.registaration,
main.anime,
.login-user-cabinet,
.main.full-desc-item,
.main.contacts {
	padding-top: 8rem;
}
.main.new-list {
	padding-top: 7rem;
}
.login-user-cabinet,
.main.full-desc-item,
.main.contacts {
	// padding-top: 8rem;
}

.main.new-list {
	// padding-top: 87px;
}
.main.contacts {
	// padding-top: 68px;
}
.container.main.login,
main.registaration {
	padding-top: 110px;
	padding-bottom: 30px;
}
@media (max-width: 358px) {
	main.error,
	main.login,
	main.registaration,
	main.anime,
	.main.contacts {
		padding-top: 145px;
	}
	.main.full-desc-item {
		padding-top: 130px;
	}
	.login-user-cabinet {
		padding-top: 129px;
	}
}
.container {
	max-width: var(--max-width);
	&.login {
		overflow: hidden;
	}
}
@media (max-width: 650px) {
	.container {
		width: 100vw;
		max-width: var(--max-width);
		&.login {
			overflow: hidden;
		}
	}
}

// buttons ----------------------------------------------------------
.btn {
	background-color: var(--btn-send-com);
	border-radius: 0.4rem;
	padding: 1rem;
	transition: all 0.25s ease-in-out;

	&:hover {
		background-color: var(--btn-send-com);
		color: var(--color-active-link);
		box-shadow: -3px -3px 3px #888;
		transform: translate(2px, 2px);
	}
	&:active {
		background-color: var(--btn-send-com);
		box-shadow: none;
		transform: translate(-2px, -2px);
	}
}
.btn-reset-pwd {
	transition: color 0.3s ease-in-out;
	&:hover {
		color: red;
	}
}
#search-header-btn {
	transition: all 0.15s ease-in-out;

	&:hover {
		color: var(--color-active-link);
	}
}

#_submit-login,
#_submit-registration {
	&:hover {
		color: var(--color-active-link);
	}
}
#btn-del-avatar {
	padding: 0.3rem 0.5rem;
	// background-color: var(--btn-send-com);
	border: 1px solid #000;
	border-radius: 0.4rem;
	transition: color 0.3s ease-in-out;

	&:hover {
		color: var(--nik-name-red);
	}
}
.btn-media-left {
	max-width: 11rem;
	width: 100%;
	margin-bottom: 0.8rem;
}
.form__btn-close {
	max-width: 4rem;
}
